import styled from 'styled-components';

const Styles=styled.div ` table {
    width: 100%;
    border-spacing: 0;
    border: 1px solid black;
    tr {
        :last-child {
            td {
                border-bottom: 0;
            }
        }
    }
    th,
    td {
        margin: 0;
        padding: 1rem;
        border-bottom: 1px solid black;
        border-right: 1px solid black;
        :last-child {
            border-right: 0;
        }
    }
}
`
export default Styles;